.table-option{
    .ivu-drawer-header-inner{
        color: #ffffff;
    }
    .db-table {
        padding-bottom: 20px;
        &-header {
            background: #f3f4f5;
            margin-bottom: 5px;

            .ivu-col {
                display: flex;
                align-items: center;
                padding: 6px 15px;
                font-weight: 500;
                text-transform: uppercase;
                font-size: 11px;

                &.center {
                    justify-content: center;
                }
            }
        }

        &-body {
            height: calc(100% - 60px);
            overflow-y: auto;
        }

        &-body-sub {
            height: calc(100% - 120px);
            overflow-y: auto;
        }

        &-row {
            margin-top: 0px;
            margin-bottom: 5px;
            transition: all .2s;

            &.active {
                background: lighten(#a4b0be, 20%);

                &:hover {
                    background: lighten(#a4b0be, 20%);
                }
            }

            &:hover {
                background: #f7f8f9;
            }

            .ivu-col {
                display: flex;
                align-items: center;
                padding: 5px 15px;
                min-height: 40px;

                &.center {
                    justify-content: center;
                }

                strong {
                    font-weight: 500;

                    .key {
                        color: #00cec9;
                    }
                }

                .ivu-input {
                    // max-width: 200px;
                    color: #555555;
                }

                .ivu-select {
                    // width: 250px;
                }

                .expand-toggle {
                    border: solid 1px lighten(#2D8CED, 10%);
                    border-radius: 50%;
                    height: 20px;
                    width: 20px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    transition: all .2s;

                    &:hover {
                        border-color: #2D8CED;

                        i {
                            color: #2D8CED;
                        }
                    }

                    &.active {
                        border-color: #2D8CED;
                        color: #2D8CED;
                        transform: rotate(180deg);
                    }

                    &.disabled {
                        border-color: #dedede;

                        i {
                            color: #dedede;
                        }
                    }

                    i {
                        color: lighten(#2D8CED, 10%);
                        font-size: 16px;
                    }
                }
            }
        }
    }
}
